(function() {

    // TODO: refactor to use dynamic imports
    const translationData = {
        en: require("../assets/i18n/en.json"),
        pl: require("../assets/i18n/pl.json"),
        jp: require("../assets/i18n/jp.json"),
    }

    // TODO: detect browser language
    const defaultLocale = "en";

    function translateContent(locale) {
        const translatable = document.querySelectorAll("[translation-key]");
    
        translatable.forEach((el) => {
            const translationKey = el.attributes["translation-key"].value;
            
            el.innerHTML = translationData[locale][translationKey] || translationKey;
        })
    }

    function setupLocaleSelectors() {
        const localeSelectors = document.getElementById("language-select").children
    
        for (let selector of localeSelectors) {
            selector.addEventListener("click", () => {
                const selectedLocale = selector.attributes["locale"].value;

                translateContent(selectedLocale);

                // TODO: update naming
                for (let selector of localeSelectors) {
                    selector.classList.remove("active")
                }

                selector.classList.add("active")
            })
        }
    }

    translateContent(defaultLocale);
    setupLocaleSelectors();
})()
